@import "../../global.scss";


.intro {
    background-color: white;
    display: flex;
  
    @include mobile{
      flex-direction: column;
      align-items: center;
    }
    /*lado izquierdo de la introduccion*/
    .left {
      flex: 0.5;
      overflow: hidden;
      /*contenedor de la imagen circulo, media luna*/
      .imgContainer {
        width: 700px;
        height: 700px;
        background-color: crimson;
        border-radius: 50%;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        float: right;
  
        @include mobile{
          align-items: flex-start;
        }
        /*Tama√±o de img*/
        img {
          height: 90%;
  
          @include mobile{
            height: 60%;
          }
        }
      }
    }
    /*Lado izquierdo*/
    .right {
      flex: 0.5;
      position: relative;
      /* contenedor de mis datos */
      .wrapper {
        width: 100%;
        height: 100%;
        padding-left: 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;
  
        @include mobile{
          padding-left: 0;
          align-items: center;
        }
  
        h1 {
          font-size: 60px;
          margin: 10px 0;
  
          @include mobile{
            font-size: 40px;
          }
        }
        h2 {
          font-size: 35px;
        }
        h3 {
          font-size: 30px;
  
          @include mobile{
            font-size: 20px;
          }
  
          span {
            font-size: inherit;
            color: crimson;
          }
  
          .ityped-cursor {
            animation: blink 1s infinite;
          }
          /* mouse sobre el ityped */
          @keyframes blink {
              50%{
                  opacity: 1;
              }
              100%{
                  opacity: 0;
              }
          }
        }
      }
      /*contenedor de la flecha para ir a portafolio*/
      a {
        position: absolute;
        bottom: 10px;
        left: 40%;
  
        img {
          width: 30px;
          animation: arrowBlink 2s infinite;
        }
        /*Flecha para moverme a el portafolio*/
        @keyframes arrowBlink {
          100% {
            opacity: 0;
          }
        }
      }
    }
  }
  