/* scroll-snap-type: y mandatory; 
   scroll-snap-align: start;
codigo para generar el efecto del scroll
    &::-webkit-scrollbar{
            display: none;
        }
codigo para eliminar el scroll
*/

.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        background-color:black;
        position:relative;
        top:70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none;
        &::-webkit-scrollbar{
            display: none;
        }
        > *{
            width: 100vw;
            height:  calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}